import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { PortalComponent } from './portal.component';

import { HeaderComponent } from './components/header/header.component';
import { SidebarComponent } from './components/sidebar/sidebar.component';

@NgModule({
    imports: [
        RouterModule.forChild([
            {
                path: '',
                component: PortalComponent,
                children: [
                    //{
                    //    path: '',
                    //    redirectTo: 'dashboard',
                    //    pathMatch: 'full'
                    //},
                    {
                        path: '',
                        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\modules\\dashboard\\dashboard.module')['PortalDashboardModule']);  });}),
                    },
                    {
                        path: 'article',
                        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\modules\\article\\article.module')['PortalArticleModule']);  });}),
                    },
                    {
                        path: 'blank',
                        loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('.\\modules\\_blank\\something.module')['PortalSomethingModule']);  });}),
                    }
                ]
            },
        ])
    ],
    exports: [RouterModule],
    declarations: [
        PortalComponent,
        HeaderComponent,
        SidebarComponent,
    ]
})

export class PortalModule { }   


